import React from "react";
import { Switch, BrowserRouter, Route } from "react-router-dom";

import Home from "../pages/Home";
import About from "../pages/About";
import Portfolio from "../pages/Portfolio";

export default function Routes() {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/website-portfolio-reactjs" component={Home} />
        <Route
          exact
          path="/website-portfolio-reactjs/about"
          component={About}
        />
        <Route
          exact
          path="/website-portfolio-reactjs/portfolio"
          component={Portfolio}
        />
      </Switch>
    </BrowserRouter>
  );
}
